___CSS_LOADER_EXPORT___.push([module.id, `body {
    height: 100svh;
}

.home-section {
    height: 80%;
    display: grid;
    place-items: center;
}

.home-section > h1 {
    background-color: rgb(139,157,219);
    background: -webkit-linear-gradient(180deg, hsla(227, 53%, 70%, 1) 0%, hsla(306, 53%, 58%, 1) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    /* box-shadow: ; */
    letter-spacing: .1rem;
}

.home-section > h1 .header-title {
    display: block;
    width: 100%;
}

.home-section > h1 .homeWelcomeWords {
    background: linear-gradient(270deg, #8a9cdb, #cd5bc1);
    background-size: 400% 400%;
    -webkit-animation: AnimationName 5s ease infinite;
    -moz-animation: AnimationName 5s ease infinite;
    animation: AnimationName 5s ease infinite;
    border-radius: 10px;
    padding: 10px;
}

.home-section > h1 .welcomeWord {
    -webkit-text-fill-color: var(--text);
    text-decoration: underline;
    position: relative;
}

/* .home-section > h1 .welcomeWord::after {
    content: ",";
    text-decoration: unset;
} */

@-webkit-keyframes AnimationName {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@-moz-keyframes AnimationName {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@keyframes AnimationName {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
