    height: 100svh;
}

.notes {
    height: 79%;
}

.notes > form {
    height: 100%;
}

.notes > form > .text-area {
    width: 100%;
    height: 100%;
    padding: 16px;
    border-radius: 15px;
    line-height: 1.5rem;
    letter-spacing: .08rem;
    resize: none;
}

